/* CONTACT ME*/



.resume_button {
  width: 15em;
  height: 2.5em;
  margin-top: 3%;
  background: #c2a4f3;
  color: white;
  border: none;
  border-radius: 0.625em;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  position: relative;
  z-index: 1;
  overflow: hidden;
 }
 
 .resume_button:hover {
  color: #c2a4f3;
 }
 
 .resume_button:after {
  content: "";
  background: white;
  position: absolute;
  z-index: -1;
  left: -20%;
  right: -20%;
  top: 0;
  bottom: 0;
  transform: skewX(-45deg) scale(0, 1);
  transition: all 0.5s;
 }
 
 .resume_button:hover:after {
  transform: skewX(-45deg) scale(1, 1);
  transition: all 0.5s;
 }
