}
.plant {
  position: absolute; /* Positions the plant over the Row */
  bottom: 0; /* Aligns the bottom of the plant with the footer */
  left: 90%; /* Centers the plant horizontally */
  transform: translateX(-50%) translateY(-200%) rotate(0deg); /* Start position (off-screen) */
  z-index: 2; /* Ensures the plant is above the Row */
  max-width: 100%; /* Ensures the plant image is responsive */
  animation: dropAndSwing 2.5s ease-out forwards, gentleSwing 2s ease-in-out 2.5s infinite;
}
@keyframes dropAndSwing {
  0% {
    transform: translateX(-50%) translateY(-200%) rotate(0deg); /* Start off-screen */
  }
  40% {
    transform: translateX(-50%) translateY(20%) rotate(-5deg); /* Drop and tilt left */
  }
  60% {
    transform: translateX(-50%) translateY(20%) rotate(5deg); /* Tilt right */
  }
  80% {
    transform: translateX(-50%) translateY(10%) rotate(-3deg); /* Settle slightly */
  }
  100% {
    transform: translateX(-50%) translateY(0%) rotate(0deg); /* Fully stable */
  }
}
@keyframes gentleSwing {
  0% {
    transform: translateX(-50%) translateY(0%) rotate(0deg);
  }
  50% {
    transform: translateX(-50%) translateY(0%) rotate(2deg);
  }
  100% {
    transform: translateX(-50%) translateY(0%) rotate(-2deg);
  }
}

.plant {
  animation: dropAndSwing 2.5s ease-out forwards, gentleSwing 2s ease-in-out 2.5s infinite; /* Faster gentle swing */
}

