/* DESIGN FOR Hello*/
@font-face {
  font-family: "Rocher";
  src: url(https://assets.codepen.io/9632/RocherColorGX.woff2);
}

@font-palette-values --Mint {
  font-family: Rocher;
  base-palette: 3;
}

@font-palette-values --HELLO {
  font-family: Rocher;
  base-palette: 3;
}

.body_header {
  text-align: center;
}

.mint {
  font-palette: --Mint;
  font-family: "Rocher";
}

.say_hello {
  font-palette: --HELLO;
  font-family: "Rocher";
}
.language_name{
  font-palette: --HELLO;
  font-family: "Rocher";
  margin: 0;
}
/* Text EFFECT PEACOCK USED IN PROJECT NAME */

.peackok_heading {
  color: #95b5df;
  font-family: "Rouge Script", cursive;
  font-size: 30px;
  font-weight: normal;
  line-height: 48px;
  margin: 0 0 50px;
  text-align: center;
  text-shadow: 1px 1px 2px #082b34;
}

.language_text {
  text-shadow: 1px 1px 0 var(--color-secondary),
    2px 2px var(--color-quaternary), 3px 3px 0 var(--color-quinary);
  font-family: "Bungee", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  font-size: large;
  margin: 0;
  color: var(--color-pink);
  animation: shadowsmall 1.2s ease-in infinite, moves 1.2s ease-in infinite;
  letter-spacing: 0.2rem; /* Adjusted for better readability */
  margin-bottom: 1rem;
}

@keyframes shadowsmall {
  0% {
    text-shadow: none;
  }

  10% {
    text-shadow: 1px 1px 0 var(--color-secondary);
  }

  20% {
    text-shadow: 1px 1px 0 var(--color-secondary),
      2px 2px 0 var(--color-tertiary);
  }

  30% {
    text-shadow: 1px 1px 0 var(--color-secondary),
      2px 2px 0 var(--color-tertiary), 3px 3px var(--color-quaternary);
  }

  40% {
    text-shadow: 1px 1px 0 var(--color-secondary),
      2px 2px 0 var(--color-tertiary), 3px 3px var(--color-quaternary),
      4px 4px 0 var(--color-quinary);
  }

  50% {
    text-shadow: 1px 1px 0 var(--color-secondary),
      2px 2px 0 var(--color-tertiary), 3px 3px var(--color-quaternary),
      4px 4px 0 var(--color-quinary);
  }

  60% {
    text-shadow: 1px 1px 0 var(--color-secondary),
      2px 2px 0 var(--color-tertiary), 3px 3px var(--color-quaternary),
      4px 4px 0 var(--color-quinary);
  }

  70% {
    text-shadow: 1px 1px 0 var(--color-secondary),
      2px 2px 0 var(--color-tertiary), 3px 3px var(--color-quaternary);
  }

  80% {
    text-shadow: 1px 1px 0 var(--color-secondary),
      2px 2px 0 var(--color-tertiary);
  }

  90% {
    text-shadow: 1px 1px 0 var(--color-secondary);
  }

  100% {
    text-shadow: none;
  }
}


@keyframes moves {
  0% {
    transform: translate(0px, 0px);
  }

  40% {
    transform: translate(-2px, -2px);
  }

  50% {
    transform: translate(-2px, -2px);
  }

  60% {
    transform: translate(-2px, -2px);
  }

  100% {
    transform: translate(0px, 0px);
  }
}
/* DESIGN FOR PROJECT */

#text_shadows {
  text-shadow: 3px 3px 0 var(--color-secondary), 6px 6px 0 var(--color-tertiary),
    9px 9px var(--color-quaternary), 12px 12px 0 var(--color-quinary);
  font-family: "Bungee", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  font-size: calc(2rem + 5vw);
  text-align: center;
  margin: 0;
  color: var(--color-pink);
  animation: shadows 1.2s ease-in infinite, move 1.2s ease-in infinite;
  letter-spacing: 0.4rem;
}

@keyframes shadows {
  0% {
    text-shadow: none;
  }

  10% {
    text-shadow: 3px 3px 0 var(--color-secondary);
  }

  20% {
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary);
  }

  30% {
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary), 9px 9px var(--color-quaternary);
  }

  40% {
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary), 9px 9px var(--color-quaternary),
      12px 12px 0 var(--color-quinary);
  }

  50% {
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary), 9px 9px var(--color-quaternary),
      12px 12px 0 var(--color-quinary);
  }

  60% {
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary), 9px 9px var(--color-quaternary),
      12px 12px 0 var(--color-quinary);
  }

  70% {
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary), 9px 9px var(--color-quaternary);
  }

  80% {
    text-shadow: 3px 3px 0 var(--color-secondary),
      6px 6px 0 var(--color-tertiary);
  }

  90% {
    text-shadow: 3px 3px 0 var(--color-secondary);
  }

  100% {
    text-shadow: none;
  }
}

@keyframes move {
  0% {
    transform: translate(0px, 0px);
  }

  40% {
    transform: translate(-12px, -12px);
  }

  50% {
    transform: translate(-12px, -12px);
  }

  60% {
    transform: translate(-12px, -12px);
  }

  100% {
    transform: translate(0px, 0px);
  }
}

/* FOOTER BUTTON */

.footer .contact-email,
.footer .contact-email:after {
  width: 300px;
  height: 130px;
  line-height: 130px;
  font-size: 35px;
  font-family: "Bungee", sans-serif;
  background: linear-gradient(45deg, transparent 5%, #99ccff 5%);
  border: 0;
  color: #fff;
  letter-spacing: 3px;
  box-shadow: 5px 0px 0px #c2a4f3;
  outline: transparent;
  position: relative;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.footer .contact-email:after {
  --slice-0: inset(50% 50% 50% 50%);
  --slice-1: inset(80% -6px 0 0);
  --slice-2: inset(50% -6px 30% 0);
  --slice-3: inset(10% -6px 85% 0);
  --slice-4: inset(40% -6px 43% 0);
  --slice-5: inset(80% -6px 5% 0);

  content: "ALTERNATE TEXT";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg,
      transparent 3%,
      #99ccff 3%,
      #99ccff 5%,
      #d4e2d4 5%);
  text-shadow: -3px -3px 0px #f8f005, 3px 3px 0px #99ccff;
  clip-path: var(--slice-0);
}

.footer .contact-email:hover:after {
  animation: 1s glitch;
  animation-timing-function: steps(2, end);
}

@keyframes glitch {
  0% {
    clip-path: var(--slice-1);
    transform: translate(-20px, -10px);
  }

  10% {
    clip-path: var(--slice-3);
    transform: translate(10px, 10px);
  }

  20% {
    clip-path: var(--slice-1);
    transform: translate(-10px, 10px);
  }

  30% {
    clip-path: var(--slice-3);
    transform: translate(0px, 5px);
  }

  40% {
    clip-path: var(--slice-2);
    transform: translate(-5px, 0px);
  }

  50% {
    clip-path: var(--slice-3);
    transform: translate(5px, 0px);
  }

  60% {
    clip-path: var(--slice-4);
    transform: translate(5px, 10px);
  }

  70% {
    clip-path: var(--slice-2);
    transform: translate(-10px, 10px);
  }

  80% {
    clip-path: var(--slice-5);
    transform: translate(20px, -10px);
  }

  90% {
    clip-path: var(--slice-1);
    transform: translate(-10px, 0px);
  }

  100% {
    clip-path: var(--slice-1);
    transform: translate(0);
  }
}

/* ADRELLA TEXT */
.adrella_header {
  color: var(--color-green);
  font-family: "Great Vibes", cursive;
  font-weight: bold;
  text-align: center;
  text-shadow: 0 1px 1px #fff;
}

.adrella_text {
  color: #7a7c7f;
  font-size: 18px;
  font-family: "Libre Baskerville", serif;
  line-height: 40px;
  text-shadow: 0 1px 1px #fff;
}

.adrella_text.byline {
  font-style: italic;
  color: #b6b6b6;
  font-size: 24px;
  margin-top: 20px;
  text-align: center;
  text-shadow: 0 1px 1px #fff;
}

.adrella_text_small {
  color: #7a7c7f;
  font-size: 15px;
  font-family: "Libre Baskerville", serif;
  font-weight: 600;
  line-height: 15px;
  text-shadow: 0 1px 1px #fff;
}

@keyframes shimmer {
  to {
    background-size: 100% 100%;
    box-shadow: -2px -2px 6px rgba(255, 227, 126, 0.5),
      2px 2px 6px rgba(144, 148, 255, 0.5);
  }
}

/* 
TEXT JUMPING */

.body_text span {
  font-weight: bold;
}

@media only screen and (max-width: 600px) {
  .adrella_text {
    color: #7a7c7f;
    font-size: 15px;
    font-family: "Libre Baskerville", serif;
    line-height: 35px;
    text-shadow: 0 1px 1px #fff;
  }
  .peackok_heading {
    color: var(--color-green);
    font-family: "Rouge Script", cursive;
    font-size: 30px;
    font-weight: normal;
    line-height: 30px;
    margin: 0 0 40px;
    text-align: center;
    text-shadow: 1px 1px 2px #082b34;
  }
  .footer .contact-email,
  .footer .contact-email:after {
    width: 200px;
    height: 90px;
    line-height: 90px;
    font-size: 20px;
    font-family: "Bungee", sans-serif;
    background: linear-gradient(45deg, transparent 5%, #99ccff 5%);
    border: 0;
    color: #fff;
    letter-spacing: 3px;
    box-shadow: 5px 0px 0px #c2a4f3;
    outline: transparent;
    position: relative;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
  }

  .footer .contact-email:after {
    --slice-0: inset(50% 50% 50% 50%);
    --slice-1: inset(80% -4px 0 0);
    --slice-2: inset(50% -4px 30% 0);
    --slice-3: inset(10% -4px 85% 0);
    --slice-4: inset(40% -4px 43% 0);
    --slice-5: inset(80% -4px 5% 0);

    content: "ALTERNATE TEXT";
    display: block;
    position: absolute;
    top: 0; /* Ensures alignment with the button */
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(45deg,
        transparent 3%,
        #99ccff 3%,
        #99ccff 5%,
        #d4e2d4 5%);
    text-shadow: -2px -2px 0px #f8f005, 2px 2px 0px #99ccff;
    clip-path: var(--slice-0);
    z-index: 1; /* Ensures it stays above the button */
  }

  .footer .contact-email:hover:after {
    animation: 1s glitch;
    animation-timing-function: steps(2, end);
  }

  @keyframes glitch {
    0% {
      clip-path: var(--slice-1);
      transform: translate(-15px, -8px);
    }

    10% {
      clip-path: var(--slice-3);
      transform: translate(8px, 8px);
    }

    20% {
      clip-path: var(--slice-1);
      transform: translate(-8px, 8px);
    }

    30% {
      clip-path: var(--slice-3);
      transform: translate(0px, 4px);
    }

    40% {
      clip-path: var(--slice-2);
      transform: translate(-4px, 0px);
    }

    50% {
      clip-path: var(--slice-3);
      transform: translate(4px, 0px);
    }

    60% {
      clip-path: var(--slice-4);
      transform: translate(4px, 8px);
    }

    70% {
      clip-path: var(--slice-2);
      transform: translate(-8px, 8px);
    }

    80% {
      clip-path: var(--slice-5);
      transform: translate(15px, -8px);
    }

    90% {
      clip-path: var(--slice-1);
      transform: translate(-8px, 0px);
    }

    100% {
      clip-path: var(--slice-1);
      transform: translate(0);
    }
  }


}